.divNav {
  position: relative;
  top: 0;
  width: 100%;
  height: 50px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  -webkit-backdrop-filter: brightness(0.4);
          backdrop-filter: brightness(0.4);
  /* background-color: #2A2438; */
  /* background-color: hsla(0, 0%, 0%, 0.904); */
}

.divNav .leftSide {
  flex: 50% 1;
  display: flex;
  justify-content: left;
  align-items: center;
}

.divNav .leftSide .divLinks {
  max-height: 80px;
}

.divNav .leftSide .divLinks a {
  font-size: 18px;
  margin-left: 18px;
}

.divNav .leftSide button {
  display: none;
  margin-left: 20px;
}

.divNav .leftSide #hidden {
  display: flex;
}

.divNav .rightSide {
  flex: 50% 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 20px;
}

@media only screen and (max-width: 850px) {
  .divNav .leftSide .divLinks a {
    font-size: 16px;
  }
}

