  .divNav .leftSide .divLinks a {
    font-size: 14px;
    margin-left: 10px;
  }
}

@media only screen and (max-width: 650px) {
  .divNav {
    background-color:#393b3d;;
  }
  .divNav .leftSide button {
    display: flex;
  }

  .divNav .leftSide .divLinks {
    display: none;
  }

  .divNav .leftSide #hidden {
    position: absolute;
    left: 0px;
    top: 50px;
    height: 230px;
    max-height: 95px;
    width: 100%;
    -webkit-backdrop-filter: brightness(0.2);
            backdrop-filter: brightness(0.2);
    background-color:#393b3d;
    /* background-color: rgba(0, 0, 0, 0.555); */
    display: flex;
    flex-direction: column;
    /* align-items: center; */
  }

  .divNav .leftSide #hidden a {
    margin: 1px;
    padding: 5px;
    padding-left: 20px;
    font-size: 15px;
    border-bottom: 0.1px solid var(--primary-font-color);
    width: 100%;
    font-weight: bold;
  }

  .divNav .leftSide #hidden a:hover {
   color: #BBBBBB;
   
  
  }
}

